const meta: Meta<typeof AutoCompleteFilterMultipleSelectMultipleFieldsDisplay> =
  {
    title:
      'Components/AutoComplete/AutoCompleteFilterMultipleSelectMultipleFieldsDisplay',
    component: AutoCompleteFilterMultipleSelectMultipleFieldsDisplay,
    tags: ['autodocs'],
    parameters: {
      componentSubtitle: `import { AutoCompleteFilterMultipleSelectMultipleFieldsDisplay } from 'react-restyle-components'`,
      layout: 'centered',
    },
    decorators: [
      (Story) => (
        <Wrapper>
          <Story />
        </Wrapper>
      ),
    ],
  };
export default meta;
type Story = StoryObj<
  typeof AutoCompleteFilterMultipleSelectMultipleFieldsDisplay
>;

const Template = (
  args: AutoCompleteFilterMultipleSelectMultipleFieldsDisplayProps
) => {
  const [selectedItems, setSelectedItems] = useState(args.data.selected);
  const [filteredData, setFilteredData] = useState(args.data);

  const handleUpdate = (item) => {
    const updatedSelectedItems = selectedItems.includes(item)
      ? selectedItems.filter((selectedItem) => selectedItem !== item)
      : [...selectedItems, item];
    setSelectedItems(updatedSelectedItems);
    action('onSelect')(updatedSelectedItems);
  };
  return (
    <AutoCompleteFilterMultipleSelectMultipleFieldsDisplay
      {...args}
      data={{...filteredData, selected: selectedItems}}
      onFilter={(value) => {
        if (value.trim() === '') {
          setFilteredData(args.data);
        } else {
          const filteredOptions = args.data?.list?.filter(
            (item: {description: string}) =>
              item.description.toLowerCase().includes(value.toLowerCase())
          );
          setFilteredData({...args.data, list: filteredOptions});
        }
        action('onFilter')(value);
      }}
      onSelect={handleUpdate}
    />
  );
};

